import * as React from 'react';
import Modal from '../UI/Modal/Modal';
import { Zone } from '../../model/Configuration';
import { getZoneReaderIPClassName } from '../../../utility/getClassesValue';

interface ZoneFormProps {
    zone: Zone;
    title: string;
    propName?: string;
    index: number;
    onChange: (event: React.ChangeEvent<HTMLInputElement>, propName: string, index?: number) => void;
    requiredClass: string;
    removeZone: (event: React.FormEvent<HTMLButtonElement>, index: number) => void;
}

class ZoneForm extends React.Component<ZoneFormProps> {

    state = {
        showModal: false
    };

    getRequiredClass = (field: string): string => {
        return field ? '' : 'input-error';
    }

    getGTZeroClass = (field: number): string => {
        return field && field > 0 ? '' : 'input-error';
    }

    getRangeClass = (field: number, min: number, max: number): string => {
        return field && field > min && field < max ? '' : 'input-error';
    }

    zoneRemovalConfirm = () => {
        this.setState({
            showModal: true
        });
    }

    zoneRemovalConfirmation = (event: React.FormEvent<HTMLButtonElement>, index: number) => {
        this.props.removeZone(event, index);

        this.setState({
            showModal: false
        });
    }

    handleClose = () => {
        this.setState({
            showModal: false
        });
    }

    render () {

        return (        
            <div 
                className={
                    'container-conf'  + (this.props.index !== undefined && this.props.index > -1 ? '-zone' : '')
                }
            >
                {
                    this.props.index !== undefined && this.props.index > -1 ? (<h3 className="test">
                    Zone {this.props.zone.zoneLocation}
                            <input 
                                className="remove-zone"
                                onClick={this.zoneRemovalConfirm} 
                                type="button" 
                                value="Remove Zone"
                            />
                    </h3>)
                    :
                    (<h3>{this.props.title}</h3>)
                }
                <div>
                    <label htmlFor="zoneLocation">Zone Location</label>
                    <input
                        type="text"
                        name="zoneLocation"
                        value={this.props.zone.zoneLocation}
                        onChange={
                            (event) => this.props.onChange(
                                event, 
                                this.props.propName ? this.props.propName : 'zoneLocation', 
                                this.props.index
                            )
                        }
                        className={this.getRequiredClass(this.props.zone.zoneLocation)}
                    />
                </div>
                <div>
                    <label htmlFor="zoneUnknownAssetsLocation">Zone Unknown Assets Location</label>
                    <input
                        type="text"
                        name="zoneUnknownAssetsLocation"
                        value={this.props.zone.zoneUnknownAssetsLocation}
                        onChange={
                            (event) => this.props.onChange(
                                event, 
                                this.props.propName ? this.props.propName : 'zoneUnknownAssetsLocation', 
                                this.props.index
                            )
                        }
                        className={this.getRequiredClass(this.props.zone.zoneUnknownAssetsLocation)}
                    />
                </div>
                <div>
                    <label htmlFor="zoneMinimumRSSI">Zone Minimum RSSI</label>
                    <input
                        type="text"
                        name="zoneMinimumRSSI"
                        value={this.props.zone.zoneMinimumRSSI}
                        onChange={
                            (event) => this.props.onChange(
                                event, 
                                this.props.propName ? this.props.propName : 'zoneMinimumRSSI', 
                                this.props.index
                            )
                        }
                        className={this.getGTZeroClass(this.props.zone.zoneMinimumRSSI)}
                    />
                </div>
                <div>
                    <label htmlFor="zoneReaderIP">Zone Reader IP</label>
                    <input
                        type="text"
                        name="zoneReaderIP"
                        value={this.props.zone.zoneReaderIP}
                        onChange={
                            (event) => this.props.onChange(
                                event, 
                                this.props.propName ? this.props.propName : 'zoneReaderIP', 
                                this.props.index
                            )
                        }
                        className={getZoneReaderIPClassName(this.props.zone.zoneReaderIP)}
                    />
                </div>
                <div>
                    <label htmlFor="zoneReaderPort">Zone Reader Port</label>
                    <input
                        type="text"
                        name="zoneReaderPort"
                        value={this.props.zone.zoneReaderPort}
                        onChange={
                            (event) => this.props.onChange(
                                event, 
                                this.props.propName ? this.props.propName : 'zoneReaderPort', 
                                this.props.index
                            )
                        }
                        className={this.getRangeClass(this.props.zone.zoneReaderPort, 0, 65536)}
                    />
                </div>
                <Modal 
                    show={this.state.showModal} 
                >
                    <h4>Are you sure you want to remove the zone?</h4>
                    <button 
                        id="btnSubmit"
                        type="text" 
                        onClick={
                            (event: React.FormEvent<HTMLButtonElement>) => 
                                this.zoneRemovalConfirmation(event, this.props.index)
                        }
                    >Submit
                    </button>
                    <button className="declineModalButton" type="button" onClick={this.handleClose}>Close</button>
                </Modal>
            </div>  
        );
    }    
}

export default ZoneForm;


