import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { Route } from 'react-router-dom';
import createBrowserHistory from 'history/createBrowserHistory';
import SettingsConfiguration from './settings/SettingsConfiguration';
import User from './user/User';

import './AdminPanel.css';

export interface AdminPanelProps {
}

interface AdminPanelState {
    username: string;
    atbServerUrl: string;
}

const history = createBrowserHistory({ forceRefresh: true });

class AdminPanel extends React.Component<AdminPanelProps, AdminPanelState> {

    state: AdminPanelState = {
        username: '',
        atbServerUrl: ''
    };

    componentWillMount() {
        const atbServerUrl = localStorage.getItem('atbServerUrl');
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        fetch('http://' + atbServerUrl + '/user/login/', {
            headers: {
                'credentials': username + ':' + password
            }
        }).then(response => {
            if (!response.ok) {
                throw Error(response.status.toString());
            }

            if (username) {
                this.setState({
                    username: username
                });
            }

            if (atbServerUrl) {
                this.setState({
                    atbServerUrl: atbServerUrl
                });
            }
        }).catch(e => {
            history.push('/');
        });
    }

    logout = (event: React.FormEvent<HTMLButtonElement>) => {
        event.preventDefault();
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        console.warn(username + ' ' + password);
        fetch('http://' + this.state.atbServerUrl + '/user/logout/', {
            method: 'POST',
            headers: {
                'credentials': username + ':' + password
            }
        }).then((response) => {
            localStorage.removeItem('username');
            localStorage.removeItem('password');
            history.push('/');
        });
    }

    render() {
        return (
            <div>
                <div className="App-content">
                    <nav className="App-menu">
                        <NavLink to="/admin/user" activeClassName="active" exact={true}>
                            User View
                        </NavLink >
                        <NavLink to="/admin/configuration" activeClassName="active" exact={true}>
                            Configuration View
                        </NavLink>
                        <div className="AdminPanel-user">
                            <span>Username: <b>{this.state.username}</b></span>
                            <button id="logoutBtn" onClick={(event) => this.logout(event)}>Logout</button>
                        </div>
                    </nav>
                    <Route path="/admin/user" exact={true} component={User} />
                    <Route path="/admin/configuration" exact={true} component={SettingsConfiguration} />
                </div>
            </div>
        );
    }
}

export default AdminPanel;



