import { validEmail, validateIPaddress } from './isFormValid';

export function getRequiredClass(field: string): string {
    return field ? '' : 'input-error';
}

export function getGTZeroClass(field: number): string {
    return field && field > 0 ? '' : 'input-error';
}

export function getRangeClass(field: number, min: number, max: number): string {
    return field && field > min && field < max ? '' : 'input-error';
}

export function getEmailClassName(field: string): string {
    return field && validEmail(field) ? '' : 'input-error';
}

export function getPortClassName(field: number): string {
    return field && 
    field !== undefined &&
    field.toString() !== '' && 
    field && 
    field <  65536 ? '' : 'input-error';
}

export function getZoneReaderIPClassName(field: string): string {
    return field && 
    validateIPaddress(field) && 
    field !== undefined && 
    field !== '' ? '' : 'input-error';
}


