import * as React from 'react';
import createBrowserHistory from 'history/createBrowserHistory';
import TabButton from '../../components/UI/TabButton/TabButton';
import EmailFields from '../../components/Email/Email';
import GeneralFields from '../../components/General/General';
import './SettingsConfiguration.css';
import { SettingsConfigurationState } from '../../model/Configuration';
import Zones from '../../components/Zones/Zones';
import { getRequiredClass } from '../../../utility/getClassesValue';
import Modal from '../../components/UI/Modal/Modal';
import { isFormValide, 
         emailValidation, 
         generalValidation, 
         validateStorageLocation,
         validZones
} from '../../../utility/isFormValid';

const history = createBrowserHistory({ forceRefresh: true });

// tslint:disable-next-line:max-line-length
// const ipregex = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;

class SettingsConfiguration extends React.Component<{}, SettingsConfigurationState> {

    state: SettingsConfigurationState = {
        configuration: {
            hostUrl: '',
            username: '',
            password: '',
            operationMode: 'SYNC',
            smtpHost: '',
            smtpPort: 0,
            smtpUser: '',
            smtpPassword: '',
            emailTo: '',
            emailSubject: '',
            emailFrom: '',
            assetTagPresentCount: 0,
            assetTagTimeout: 0,
            assetTrackingStatusAttribute: '',
            assetTagIdAttribute: '',
            assetTagBatteryStatusAttribute: '',
            assetTagHWRevisionAttribute: '',
            assetTagSWRevisionAttribute: '',
            zoneReaderIPAddressAttribute: '',
            zoneReaderStatusAttribute: '',
            unknownAssetTemplate: '',
            unknownAssetLabel: '',
            decommissionedAssetsLocation: '',
            storage: {
                zoneLocation: '',
                zoneUnknownAssetsLocation: '',
                zoneMinimumRSSI: 0,
                zoneReaderIP: '',
                zoneReaderPort: 0
            },
            staging: {
                zoneLocation: '',
                zoneUnknownAssetsLocation: '',
                zoneMinimumRSSI: 0,
                zoneReaderIP: '',
                zoneReaderPort: 0
            },
            readerOfflineTries: 0,
            readerOnlineTries: 0,
            woConfirmationPhase: '',
            woExecutedPhase: '',
            zones: []
        },
        atbServerUrl: '',
        currentAtbServerUrl: '',
        isDataFetched: false,
        isFormValid: false,
        logLevel: 'INFO',
        username: '',
        displayGeneralTab: false,
        displayZonesTab: false,
        displayEmailTab: true,
        modalToggler: false
    };

    componentWillMount() {
        const atbServerUrl = localStorage.getItem('atbServerUrl');
        const username = localStorage.getItem('username');
        if (username) {
            this.setState({
                username: username
            });
        }

        if (atbServerUrl) {
            this.setState(
                {
                    atbServerUrl: atbServerUrl
                },
                () => {
                    this.getConfiguration();
                }
            );
        }
    }

    getLogLevel = () => {
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        fetch('http://' + this.state.atbServerUrl + '/loggers/com.rm.atb/', {
            headers: {
                'credentials': username + ':' + password
            }
        })
            .then(results => {
                return results.json();
            }).then(data => {
                this.setState({
                    logLevel: data.configuredLevel
                });
            });
    }

    setLogLevel = (newLevel: string) => {
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        fetch('http://' + this.state.atbServerUrl + '/loggers/com.rm.atb/', {
            method: 'POST',
            headers: {
                'credentials': username + ':' + password,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                'configuredLevel': newLevel
            })
        });
    }

    getConfiguration = () => {
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        fetch('http://' + this.state.atbServerUrl + '/settings/', {
            method: 'GET',
            headers: {
                'credentials': username + ':' + password
            }
        }).then(response => {
            if (!response.ok) {
                throw Error(response.status.toString());
            }
            return response.json();
        }).then(data => {
            this.setState(
                {
                    configuration: data,
                    isDataFetched: true,
                    currentAtbServerUrl: this.state.atbServerUrl
                },
                () => {
                    this.isFormValid();
                    this.getLogLevel();
                }
            );
        }).catch(error => {
            history.push('/');
        });
    }

    handleInputChange = (
        event: React.FormEvent<HTMLInputElement | HTMLSelectElement>, type?: string, index?: number
    ) => {
        const target = event.target as HTMLFormElement;
        const value = target.value;
        const name = target.name;

        let configuration = { ...this.state.configuration };
        if (type === 'storage') {
            configuration.storage[name] = value;
        } else if (type === 'staging') {
            configuration.staging[name] = value;
        } else if (index !== -1 && index !== undefined && type) {
            configuration.zones[index][type] = value;
           
        } else if (name === 'atbServerUrl') {
            this.setState({
                atbServerUrl: value
            });
        } else if (name === 'logLevel') {
            this.setState({
                logLevel: value
            });
        } else {
            configuration[name] = value;
        }

        this.setState(
            {
                configuration: configuration
            },
            () => {
                this.isFormValid();
            }
        );
    }

     saveConfiguration = async () => {
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        this.setLogLevel(this.state.logLevel);
        try {
            await fetch('http://' + this.state.atbServerUrl + '/settings/edit/', {
                method: 'PUT',
                headers: {
                    'credentials': username + ':' + password,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(this.state.configuration)
            });
            alert('New configuration saved!');
        } catch (e) {
            // tslint:disable-next-line:no-console
            console.error(e);
            alert('Saving failed! Please try again.');
        }
        
    }

    addZone = (event: React.FormEvent<HTMLButtonElement>) => {
        event.preventDefault();
        let configuration = { ...this.state.configuration };
        let newZone = {
            zoneLocation: '',
            zoneUnknownAssetsLocation: '',
            zoneMinimumRSSI: 0,
            zoneReaderIP: '',
            zoneReaderPort: 0
        };
        configuration.zones.push(newZone);
        this.setState(
            {
                configuration: configuration
            },
            () => {
                this.isFormValid();
            }
        );
    }

    removeZone = (event: React.FormEvent<HTMLButtonElement>, index: number) => {
        event.preventDefault();
        let configuration = { ...this.state.configuration };
        configuration.zones.splice(index, 1);
        this.setState(
            {
                configuration: configuration
            },
            () => {
                this.isFormValid();
            }
        );
    }

    isFormValid = () => {
        const isValid = isFormValide(this.state.configuration);
        this.setState({
            isFormValid: isValid
        });
    
    }

    emailTabHandler = () => {
        this.setState(prevState => {
            return {
                ...this.state,
                displayEmailTab: true,
                displayGeneralTab: false,
                displayZonesTab: false
            };
        });
    }

    generalTabHandler = () => {
        this.setState(prevState => {
            return {
                ...this.state,
                displayGeneralTab: true,
                displayEmailTab: false,
                displayZonesTab: false
            };
        });
    }

    zonesTabHandler = () => {
        this.setState(prevState => {
            return {
                ...this.state,
                displayZonesTab: true,
                displayGeneralTab: false,
                displayEmailTab: false,
            };
        });
    }

    submitAndCloseModal = () => {
        this.saveConfiguration();
        this.closeModal();
    }

    closeModal = () => {
        this.setState(prevState => {
            return {
                ...this.state,
                modalToggler: false
            };
        });
    }

    displayModal = () => {
        this.setState(prevState => {
            return {
                ...this.state,
                modalToggler: true
            };
        });
    }
    
    render() {
        let form = null;
        let submitBtn = null; 

        if (this.state.isFormValid) {
            submitBtn = 'btn-enabled';
        } else {
            submitBtn = 'btn-disabled';
        }

        if (this.state.isDataFetched) {
            let emailTab = null;
            let generalTab = null;
            let zonesTab = null;
            
            if (this.state.displayEmailTab) {
                emailTab = (      
                    <EmailFields 
                                 configuration={this.state.configuration} 
                                 onInputChange={this.handleInputChange}
                        
                    />     
                );
            }
            if (this.state.displayGeneralTab) {
                generalTab = (
                    <GeneralFields 
                        configuration={this.state.configuration}
                        logLevelValue={this.state.logLevel}
                        onInputChange={this.handleInputChange}
                    />
                );
            }
            if (this.state.displayZonesTab) {
                zonesTab = (
                    <Zones 
                        config={this.state.configuration} 
                        requiredClass={getRequiredClass} 
                        handleInputChange={this.handleInputChange}
                        removeZone={this.removeZone}
                        addZone={this.addZone}
                    />
                );  
            }

            form = (
                <form>
                    <h2>Configuration of ATB Server : {this.state.currentAtbServerUrl}</h2>
                    {!this.state.displayEmailTab ? 
                        <input 
                               id="saveButtonTop" 
                               onClick={this.displayModal} 
                               type="button" 
                               className={submitBtn} 
                               disabled={!this.state.isFormValid} 
                               value="Save"
                        /> 
                    : null}
                    <section>
                        {emailTab}
                        {generalTab}
                    </section>
                        {zonesTab}
                    <input 
                           id="saveButtonBottom" 
                           onClick={this.displayModal} 
                           type="button" 
                           className={submitBtn} 
                           disabled={!this.state.isFormValid} 
                           value="Save" 
                    />

                    <Modal show={this.state.modalToggler}>
                            <h4>Do you want to save the changes? <br/> The ATB will be restarted.</h4>
                            <button
                                id="btnSubmit"
                                type="button"
                                onClick={this.submitAndCloseModal}
                            >Yes
                            </button>
                            <button id="declineModalButton" type="button" value="No" onClick={this.closeModal}>
                                No
                            </button>
                    </Modal>
                </form>
            );
        } else {
            form = null;
        }
        
        return (
            <div>
                <div>
                    <TabButton
                        name="Email" 
                        changeTab={this.emailTabHandler}
                        style={{
                            background: this.state.displayEmailTab ? '#f6e946' : 'white',
                            border: emailValidation(this.state.configuration) ? '2px solid #1f88e5' : '2px solid red',
                            transition: '1s all ease-in-out',
                            color: '#270707',
                            boxShadow: emailValidation(this.state.configuration) ? 'none' : '2px 2px red',
                            outline: 'none'
                        }} 
                    />

                    <TabButton 
                        name="General" 
                        changeTab={this.generalTabHandler} 
                        style={{
                            background: this.state.displayGeneralTab ? '#f6e946' : 'white',
                            border: generalValidation(this.state.configuration) ? '2px solid #1f88e5' : '2px solid red',
                            color: '#270707',
                            boxShadow: generalValidation(this.state.configuration) ? 'none' : '2px 2px red',
                            outline: 'none'
                            
                        }}
                    />

                    <TabButton 
                        name="Zones" 
                        changeTab={this.zonesTabHandler} 
                        style={{
                            background: this.state.displayZonesTab ? '#f6e946' : 'white',
                            border: validateStorageLocation(this.state.configuration) && 
                            validZones(this.state.configuration) ? 
                            '2px solid #1f88e5' : '2px solid red',
                            color: '#270707',
                            boxShadow: validateStorageLocation(this.state.configuration) && 
                            validZones(this.state.configuration) ? 'none' : '2px 2px red',
                            outline: 'none'
                            }}                    
                    />
                </div>
                <div className="container">
                    {form}
                </div>
            </div>
        );
    }
}

export default SettingsConfiguration;


