import * as React from 'react';
import createBrowserHistory from 'history/createBrowserHistory';

import './Login.css';

interface User {
    username: string;
    password: string;
}

interface LoginState {
    user: User;
    atbServerUrl: string;
    loginFailed: boolean;
    isFormValid: boolean;
    showPassword: boolean;
}

const history = createBrowserHistory({ forceRefresh: true });

class Login extends React.Component<{}, LoginState> {

    state: LoginState = {
        user: {
            username: '',
            password: ''
        },
        atbServerUrl: '',
        loginFailed: false,
        isFormValid: false,
        showPassword: false
    };

    handleInputChange = (event: React.FormEvent<HTMLInputElement>) => {
        const target = event.target as HTMLInputElement;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        if (name === 'atbServerUrl') {
            this.setState(
                {
                    atbServerUrl: value as string
                },
                () => {
                    this.isFormValid();
                }
            );
        } else if (name === 'showPassword') {
            this.setState({
                showPassword: value as boolean
            });
        } else {
            let user = { ...this.state.user };
            user[name] = value;
            this.setState(
                {
                    user: user
                },
                () => {
                    this.isFormValid();
                }
            );
        }
    }

    login = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        fetch('http://' + this.state.atbServerUrl + '/user/login/', {
            headers: {
                'Authorization': 'Basic ' + btoa(this.state.user.username + ':' + this.state.user.password),
                'Content-Type': 'application/x-www-form-urlencoded',
                'X-Requested-With': 'XMLHttpRequest'
            }
        }).then(response => {
            if (!response.ok) {
                throw Error(response.status.toString());
            }
            return response.json();
        }).then(data => {
            localStorage.setItem('atbServerUrl', this.state.atbServerUrl);
            localStorage.setItem('username', this.state.user.username);
            localStorage.setItem('password', this.state.user.password);
            history.push('/admin');
        }).catch(error => {
            this.setState({
                loginFailed: true
            });
        });
    }

    isFormValid = () => {
        let formValid = false;

        if (this.state.atbServerUrl !== undefined && this.state.atbServerUrl.length > 0
            && this.state.user.username !== undefined && this.state.user.username.length > 0
            && this.state.user.password !== undefined && this.state.user.password.length > 0) {
            formValid = true;
        }

        this.setState({
            isFormValid: formValid
        });
    }

    render() {
        let loginFailed = null;
        let submitBtn = null;
        let passwordType = 'password';

        if (this.state.isFormValid) {
            submitBtn = 'Login-btn-enabled';
        } else {
            submitBtn = 'Login-btn-disabled';
        }

        if (this.state.showPassword) {
            passwordType = 'text';
        }

        if (this.state.loginFailed) {
            loginFailed = <span className="login-error">Bad credentials or URL!</span>;
        }
        return (
            <form onSubmit={(event) => this.login(event)}>
                <div className="Login-container">
                    <div>
                        <label htmlFor="username">Username</label>
                        <input
                            type="text"
                            name="username"
                            value={this.state.user.username}
                            onChange={(event) => this.handleInputChange(event)}
                        />
                    </div>
                    <div>
                        <label htmlFor="password">Password</label>
                        <input
                            type={passwordType}
                            name="password"
                            value={this.state.user.password}
                            onChange={(event) => this.handleInputChange(event)}
                        />
                    </div>
                    <div>
                        <label htmlFor="atbServerUrl">ATB Server URL</label>
                        <input
                            type="text"
                            name="atbServerUrl"
                            value={this.state.atbServerUrl}
                            onChange={(event) => this.handleInputChange(event)}
                        />
                    </div>
                    <div>
                        <label htmlFor="showPassword">
                            Show password
                        </label>
                        <input
                            type="checkbox"
                            name="showPassword"
                            checked={this.state.showPassword}
                            onChange={(event) => this.handleInputChange(event)}
                        />
                    </div>
                    {loginFailed}
                </div>
                <button type="submit" className={submitBtn} disabled={!this.state.isFormValid}>LOGIN</button>
            </form>
        );
    }
}

export default Login;


