import * as React from 'react';
import Field from '../../components/Field/Field';
import { getRequiredClass, getEmailClassName, getPortClassName } from '../../../utility/getClassesValue';
import { Configuration } from '../../model/Configuration';

interface EmailProps {
    configuration: Configuration;
    onInputChange: (
        event: React.FormEvent<HTMLInputElement | HTMLSelectElement>, type?: string, index?: number
    ) => void;
}

const Email = (props: EmailProps) => {
    return (
        <div className="container-conf">
            <Field 
                htmlFor="smtpHost"
                labelName="SMTP Host"
                inputType="text" 
                inputName="smtpHost" 
                inputValue={props.configuration.smtpHost}
                inputOnChange={props.onInputChange}
                inputClassName={getRequiredClass(props.configuration.smtpHost)}
                
            />
            <Field 
                htmlFor="smtpPort"
                labelName="SMTP Port"
                inputType="text" 
                inputName="smtpPort" 
                inputValue={props.configuration.smtpPort}
                inputOnChange={props.onInputChange}
                inputClassName={getPortClassName(props.configuration.smtpPort)}
                
            />
            <Field 
                htmlFor="smtpUser"
                labelName="SMTP User"
                inputType="text" 
                inputName="smtpUser" 
                inputValue={props.configuration.smtpUser}
                inputOnChange={props.onInputChange}
                inputClassName={getRequiredClass(props.configuration.smtpUser)}
                
            />
            <Field 
                htmlFor="smtpPassword"
                labelName="SMTP Password"
                inputType="password" 
                inputName="smtpPassword" 
                inputValue={props.configuration.smtpPassword}
                inputOnChange={props.onInputChange}
                inputClassName={getRequiredClass(props.configuration.smtpPassword)}
                
            />
            <Field 
                htmlFor="emailTo"
                labelName="Email To"
                inputType="text" 
                inputName="emailTo" 
                inputValue={props.configuration.emailTo}
                inputOnChange={props.onInputChange}
                inputClassName={getEmailClassName(props.configuration.emailTo)}
                
            />
            <Field 
                htmlFor="emailSubject"
                labelName="Email Subject"
                inputType="text" 
                inputName="emailSubject" 
                inputValue={props.configuration.emailSubject}
                inputOnChange={props.onInputChange}
                inputClassName={getRequiredClass(props.configuration.emailSubject)}
                
            />
            <Field 
                htmlFor="emailFrom"
                labelName="Email From"
                inputType="text" 
                inputName="emailFrom" 
                inputValue={props.configuration.emailFrom}
                inputOnChange={props.onInputChange}
                inputClassName={getEmailClassName(props.configuration.emailFrom)}
            />
        </div>
    );
};

export default Email;


