import * as React from 'react';
import { Route } from 'react-router-dom';

import AdminPanel from './admin-panel/containers/AdminPanel';
import Login from './admin-panel/containers/login/Login';

import './App.css';

const logo = require('./logo.png');

class App extends React.Component {

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <span><img src={logo} className="App-logo" alt="logo" /></span>
          <span><h2 className="App-header-text">Asset Tracking Bridge Administrative UI</h2></span>
        </div>
        <div className="App-content">
            <Route path="/" exact={true} component={Login} />
            <Route path="/admin" component={AdminPanel} />
        </div>
      </div>
    );
  }
}

export default App;



