import * as React from 'react';
import createBrowserHistory from 'history/createBrowserHistory';

import './User.css';

interface UserState {
    username: string;
    oldPassword: string;
    newPassword: string;
    confirmPassword: string;
    isFormValid: boolean;
    isPasswordsEqual: boolean;
}

const history = createBrowserHistory({ forceRefresh: true });

class User extends React.Component<{}, UserState> {

    state: UserState = {
        username: '',
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
        isFormValid: false,
        isPasswordsEqual: false
    };

    componentWillMount() {
        const atbServerUrl = localStorage.getItem('atbServerUrl');
        const username = localStorage.getItem('username');
        const password = localStorage.getItem('password');
        fetch('http://' + atbServerUrl + '/user/login/', {
            headers: {
                'credentials': username + ':' + password
            }
        }).then(response => {
            if (!response.ok) {
                throw Error(response.status.toString());
            }
            if (username) {
                this.setState({
                    username: username
                });
            }
        }).catch(e => {
            history.push('/');
        });
    }

    editUser = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        if (!confirm('If you proceed with this action user credentials will be modified!')) {
            return;
        }
        const username = localStorage.getItem('username');
        const oldPassword = this.state.oldPassword;
        const atbServerUrl = localStorage.getItem('atbServerUrl');
        const user = {
            username: this.state.username,
            password: this.state.newPassword
        };
        fetch('http://' + atbServerUrl + '/user/edit/', {
            method: 'PUT',
            headers: {
                'credentials': username + ':' + oldPassword,
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(user)
        }).then(response => {
            if (!response.ok) {
                throw Error(response.status.toString());
            }
            localStorage.setItem('username', this.state.username);
            localStorage.setItem('password', this.state.newPassword);
            history.push('/admin/user');
        }).catch(error => {
            localStorage.removeItem('username');
            localStorage.removeItem('password');
            localStorage.removeItem('atbServerUrl');
            history.push('/');
        });
    }

    handleInputChange = (event: React.FormEvent<HTMLInputElement>) => {
        const target = event.target as HTMLInputElement;
        const value = target.value;
        const name = target.name;

        switch (name) {
            case 'confirmPassword':
                this.setState(
                    { confirmPassword: value },
                    () => {
                        this.isFormValid();
                    }
                );
                break;

            case 'newPassword':
                this.setState(
                    { newPassword: value },
                    () => {
                        this.isFormValid();
                    }
                );
                break;

            case 'oldPassword':
                this.setState(
                    { oldPassword: value },
                    () => {
                        this.isFormValid();
                    }
                );
                break;

            case 'username':
                this.setState(
                    { username: value },
                    () => {
                        this.isFormValid();
                    }
                );
                break;

            default:
                break;
        }
    }

    isFormValid = () => {
        let isFormValid = false;
        if (this.state.username !== undefined && this.state.username.length > 0
            && this.state.oldPassword !== undefined && this.state.oldPassword.length > 0
            && this.state.newPassword !== undefined && this.state.newPassword.length > 0
            && this.state.confirmPassword !== undefined && this.state.confirmPassword.length > 0) {

            isFormValid = true;
        } else {
            isFormValid = false;
        }

        if (this.state.newPassword === this.state.confirmPassword) {
            this.setState({
                isPasswordsEqual: true
            });
        } else {
            this.setState({
                isPasswordsEqual: false
            });
        }

        this.setState({
            isFormValid: isFormValid
        });
    }

    render() {
        let submitBtn = null;
        let passwordError = null;

        if (this.state.isFormValid && this.state.isPasswordsEqual) {
            submitBtn = 'Login-btn-enabled';
        } else {
            submitBtn = 'Login-btn-disabled';
        }

        if (!this.state.isPasswordsEqual && this.state.newPassword !== '') {
            passwordError = <span className="User-error-msg">Passwords are not equal</span>;
        }

        return (
            <div>
                <form onSubmit={(event) => this.editUser(event)}>
                    <div className="Login-container">
                        <div>
                            <label htmlFor="username">Username</label>
                            <input
                                type="text"
                                name="username"
                                value={this.state.username}
                                onChange={(event) => this.handleInputChange(event)}
                            />
                        </div>
                        <div>
                            <label htmlFor="oldPassword">Old password</label>
                            <input
                                type="password"
                                name="oldPassword"
                                value={this.state.oldPassword}
                                onChange={(event) => this.handleInputChange(event)}
                            />
                        </div>
                        <div>
                            <label htmlFor="newPassword">New password</label>
                            <input
                                type="password"
                                name="newPassword"
                                value={this.state.newPassword}
                                onChange={(event) => this.handleInputChange(event)}
                            />
                        </div>
                        <div>
                            <label htmlFor="confirmPassword">Confirm new password</label>
                            <input
                                type="password"
                                name="confirmPassword"
                                value={this.state.confirmPassword}
                                onChange={(event) => this.handleInputChange(event)}
                            />
                        </div>
                        {passwordError}
                    </div>
                    <button
                        type="submit"
                        className={submitBtn}
                        disabled={!(this.state.isFormValid && this.state.isPasswordsEqual)}
                    >
                        SAVE
                    </button>
                </form>
            </div>
        );
    }
}

export default User;


