import { Configuration, Zone } from '../admin-panel/model/Configuration';

export const isFormValide = (configuration: Configuration) => {
    let isZonesValid = validZones(configuration);

    if (
        emailValidation(configuration)
        && generalValidation(configuration)
        && validateStorageLocation(configuration)
        && isZonesValid
    ) {
        return true;
    }    
    return false;
};

export function validZones(configuration: Configuration) {
    let isZonesValid = true;
    if (configuration.zones.length > 0) {
        configuration.zones.map(zone => isZonesValid = validateZone(isZonesValid, zone));
    } else {
        isZonesValid = true;
    }
    return isZonesValid;
}

export function emailValidation(configuration: Configuration) {
    return validateSmtps(configuration)
        && emailInputValidation(configuration);
}

export function generalValidation(configuration: Configuration) {
    return validateHost(configuration)
    && validateCredentials(configuration)
    && validateAssets(configuration)
    && validateZoneReaders(configuration)
    && validateUnknownAssets(configuration)
    && validateDecommissioned(configuration)
    && validateReaders(configuration)
    && validateWorkOrder(configuration)
    && validateOperationMod(configuration);
}

export function validateZone(isZonesValid: boolean, zone: Zone) {
    if (isZonesValid && !(zone.zoneLocation !== undefined && zone.zoneLocation !== ''
        && zone.zoneUnknownAssetsLocation !== undefined && zone.zoneUnknownAssetsLocation !== ''
        && zone.zoneMinimumRSSI !== undefined && zone.zoneMinimumRSSI.toString() !== ''
        && zone.zoneMinimumRSSI > 0
        && zone.zoneReaderPort !== undefined && zone.zoneReaderPort.toString() !== ''
        && zone.zoneReaderPort > 0 && zone.zoneReaderPort < 65536
        && zone.zoneReaderIP !== undefined && zone.zoneReaderIP !== ''
    )) {
        isZonesValid = false;
    }
    return isZonesValid;
}

export function validateStorageLocation(configuration: Configuration): boolean {
    return (
        configuration.storage.zoneLocation !== undefined
        && configuration.storage.zoneLocation !== ''
        && configuration.storage.zoneUnknownAssetsLocation !== undefined
        && configuration.storage.zoneUnknownAssetsLocation !== ''
        && configuration.storage.zoneMinimumRSSI !== undefined
        && configuration.storage.zoneMinimumRSSI.toString() !== ''
        && configuration.storage.zoneMinimumRSSI > 0
        && configuration.storage.zoneReaderPort !== undefined
        && configuration.storage.zoneReaderPort.toString() !== ''
        && configuration.storage.zoneReaderPort > 0
        && configuration.storage.zoneReaderPort < 65536
        && configuration.storage.zoneReaderIP !== undefined
        && configuration.storage.zoneReaderIP !== ''
        && validateIPaddress(configuration.storage.zoneReaderIP)
    );
}

export function validateIPaddress(conf: string): boolean {
    let myRE = new RegExp (['^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.',
    '(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.',
    '(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.', 
    '(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$'].join(''));
    return myRE.test(conf);
}

export function validEmail(conf: string): boolean {
    let myReg = new RegExp (['^(([^<>()[\\]\\\.,;:\\s@\"]+(\\.[^<>()\\[\\]\\\.,;:\\s@\"]+)*)',
                        '|(".+"))@((\\[[0-9]{1,3}\\.[0-9]{1,3}\\.[0-9]{1,3}\\.',
                        '[0-9]{1,3}\])|(([a-zA-Z\\-0-9]+\\.)+',
                        '[a-zA-Z]{2,}))$'].join(''));
    return myReg.test(conf);
}

export function validateSmtps(configuration: Configuration): boolean {
    return (
        configuration.smtpHost !== undefined && configuration.smtpHost !== ''
        && configuration.smtpPort !== undefined && configuration.smtpPort.toString() !== ''
        && configuration.smtpPort > 0  && configuration.smtpPort <  65536
        && configuration.smtpUser !== undefined && configuration.smtpUser !== ''
        && configuration.smtpPassword !== undefined && configuration.smtpPassword !== ''
    );
}

function validateCredentials(configuration: Configuration): boolean {
    return (
         configuration.username !== undefined && configuration.username !== ''
        && configuration.password !== undefined && configuration.password !== ''
    );
}

function validateWorkOrder(configuration: Configuration): boolean {
    return (
        configuration.woConfirmationPhase !== undefined
        && configuration.woConfirmationPhase.toString() !== ''
        && configuration.woExecutedPhase !== undefined
        && configuration.woExecutedPhase.toString() !== ''
    );
}

function validateOperationMod(configuration: Configuration): boolean {
    return(configuration.operationMode !== undefined && configuration.operationMode !== ''
        && (
            configuration.operationMode === 'SYNC'
            || configuration.operationMode === 'WORKORDER'
        )
    );
}

function validateDecommissioned(configuration: Configuration): boolean {
    return (
        configuration.decommissionedAssetsLocation !== undefined
        && configuration.decommissionedAssetsLocation !== ''
    );
}

function validateHost(configuration: Configuration): boolean {
    return (
        configuration.hostUrl !== undefined && configuration.hostUrl !== ''
    );
}

function emailInputValidation(configuration: Configuration): boolean {
    return (
        configuration.emailTo !== undefined && configuration.emailTo !== ''
        && validEmail(configuration.emailTo)
        && configuration.emailSubject !== undefined && configuration.emailSubject !== ''
        && configuration.emailFrom !== undefined && configuration.emailFrom !== ''
        && validEmail(configuration.emailFrom)   
    );
}

function validateZoneReaders(configuration: Configuration): boolean {
    return (
        configuration.zoneReaderIPAddressAttribute !== undefined
        && configuration.zoneReaderIPAddressAttribute !== ''
        && configuration.zoneReaderStatusAttribute !== undefined
        && configuration.zoneReaderStatusAttribute !== ''
    );
}

function validateAssets(configuration: Configuration): boolean {
    return (
        configuration.assetTagPresentCount !== undefined
        && configuration.assetTagPresentCount.toString() !== ''
        && configuration.assetTagPresentCount > 0
        && configuration.assetTagTimeout !== undefined
        && configuration.assetTagTimeout.toString() !== ''
        && configuration.assetTagTimeout > 0
        && configuration.assetTrackingStatusAttribute !== undefined
        && configuration.assetTrackingStatusAttribute !== ''
        && configuration.assetTagIdAttribute !== undefined
        && configuration.assetTagIdAttribute !== ''
        && configuration.assetTagBatteryStatusAttribute !== undefined
        && configuration.assetTagBatteryStatusAttribute !== ''
        && configuration.assetTagHWRevisionAttribute !== undefined
        && configuration.assetTagHWRevisionAttribute !== ''
        && configuration.assetTagSWRevisionAttribute !== undefined
        && configuration.assetTagSWRevisionAttribute !== ''
    );
}

function validateUnknownAssets(configuration: Configuration): boolean {
    return (
        configuration.unknownAssetTemplate !== undefined
        && configuration.unknownAssetTemplate !== ''
        && configuration.unknownAssetLabel !== undefined
        && configuration.unknownAssetLabel !== ''
    );
}

function validateReaders(configuration: Configuration): boolean {
    return (
        configuration.readerOfflineTries !== undefined
        && configuration.readerOfflineTries.toString() !== ''
        && configuration.readerOfflineTries > 0
        && configuration.readerOnlineTries !== undefined
        && configuration.readerOnlineTries.toString() !== ''
        && configuration.readerOnlineTries > 0
    );
}


