import * as React from 'react';
import './Zones.css';
import { Zone, Configuration } from '../../model/Configuration';

import ZoneForm from '../Zones/ZoneForm';

interface ZonesProps {
    config: Configuration;
    requiredClass: (field: string) => string;
    handleInputChange: (
        event: React.FormEvent<HTMLInputElement | HTMLSelectElement>, type?: string, index?: number
    ) => void;
    removeZone: (event: React.FormEvent<HTMLButtonElement>, index: number) => void;
    addZone: (event: React.FormEvent<HTMLButtonElement>) => void;
}

const nonRemovableZoneRemove = (event: React.FormEvent<HTMLButtonElement>, index: number): void => {
    console.warn('Trying to remove non-removable zone');
};

const Zones = (props: ZonesProps) => {
        return (
            <div className="zonfeFormsWrapper">
                <section>
                    <ZoneForm 
                        zone={props.config.storage} 
                        title={props.config.storage.zoneLocation} 
                        propName={'storage'}
                        onChange={props.handleInputChange} 
                        requiredClass={props.requiredClass(props.config.storage.zoneLocation)}
                        index={-1}
                        removeZone={nonRemovableZoneRemove}
                    />

                    <ZoneForm 
                        zone={props.config.staging} 
                        title={props.config.staging.zoneLocation} 
                        propName={'staging'}
                        onChange={props.handleInputChange} 
                        requiredClass={props.requiredClass(props.config.staging.zoneLocation)}
                        index={-1}
                        removeZone={nonRemovableZoneRemove}
                    />
                </section>
                <div className="container-conf-zones">
                {props.config.zones.map((zone: Zone, index: number) =>
                    <section key={index}>
                            <ZoneForm 
                                key={index} 
                                zone={zone} 
                                title={''} 
                                index={index}
                                onChange={props.handleInputChange}
                                removeZone={props.removeZone}
                                requiredClass={props.requiredClass(zone.zoneLocation)}
                            />
                    </section>
                )}
                </div>
                <section>
                    <div className="container-conf-zone">
                        <div>
                            <button 
                                id="addZoneButton" 
                                onClick={props.addZone}
                            >
                                <i className="fa fa-plus" /> 
                                Add Zone
                            </button>
                        </div>
                    </div>
                </section>
            </div>
        );
};

export default Zones;



